@charset "UTF-8";
/* reset =============================================================================== */
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
}

body {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  width: 100%;
  font-size: 12px;
  color: #000;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  word-break: break-all;
}

html *, body * {
  font-family: "Pretendard", "sans-serif";
}

dl, ul, ol {
  list-style: none;
}

table {
  empty-cells: show;
  border-collapse: collapse;
  width: 100%;
  table-layout: fixed;
}

fieldset {
  border: none;
}

legend, caption {
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

img {
  border: none;
  vertical-align: top;
}

label {
  vertical-align: top;
}

button {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  background: none;
  -webkit-appearance: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

input, select, textarea {
  border-radius: 0;
  color: #666;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  resize: vertical;
}

ul, ol {
  list-style: none;
}

em {
  font-style: normal;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

/* common =============================================================================== */
/* align */
.tac {
  text-align: center !important;
}

.tar {
  text-align: right !important;
}

.tal {
  text-align: left !important;
}

.t_vline {
  display: inline;
  position: relative;
}

.t_vline:before {
  content: "";
  display: inline-flex;
  position: relative;
  width: 1px;
  height: 8px;
  margin-top: 0px;
  margin-right: 2px;
  margin-left: 3px;
  background-color: #aaa;
}

.cboth {
  *zoom: 1;
}

.cboth:before {
  display: table;
  content: "";
}

.cboth:after {
  display: table;
  content: "";
  clear: both;
}

/* link */
a {
  text-decoration: none;
  cursor: pointer;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
  color: #000;
  cursor: pointer;
}

/* etc */
/* 포커스라인 테스트 */
/* *:focus{outline:2px dashed green !important;padding:1px;z-index:100000;} */
*:focus {
  outline: none;
}

a, button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.03);
}

.scroll_lock {
  overflow: hidden;
  touch-action: none;
}

.hide {
  display: none !important;
}

.blind {
  position: absolute;
  overflow: hidden;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(0, 0, 0, 0);
}

.normal {
  font-weight: 400;
}

.medium {
  font-weight: 500;
}

.bold {
  font-weight: 700;
}

.skip {
  position: absolute;
  top: -100000px;
  left: -100000px;
  visibility: hidden;
  overflow: hidden;
}

.ellips {
  display: block;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.disable {
  pointer-events: none;
