/* IE10 input type auto x delete */
input[type=text]::-ms-clear, input[type=file]::-ms-clear {
  display: none;
}

::-webkit-input-placeholder, :-moz-placeholder, ::-moz-placeholder, :-ms-input-placeholder {
  color: #aaa !important;
}

input[type=text] {
  padding-right: 10px;
  padding-left: 10px;
}

input:focus {
  outline: none;
}

textarea:focus, select:focus {
  outline: none;
}

input[type=password]::-webkit-input-placeholder, input[type=password]:-moz-placeholder, input[type=password]::-moz-placeholder, input[type=password]:-ms-input-placeholder {
  font-family: "Roboto", "Noto Sans KR", "sans-serif";
}

input:-webkit-autofill {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-transition: background-color 9999s ease-out;
  -webkit-text-fill-color: inherit !important;
}

input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-transition: background-color 9999s ease-out;
  -webkit-text-fill-color: inherit !important;
}

/* layout ================================================================================================= */
.wrap {
  position: relative;
  min-height: 100%;
}

/* nav */
.nav .menu .menu_inn {
  display: flex;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  background-color: #fafafa;
}

.nav .menu .menu_inn .item {
  position: relative;
  flex: 1;
}

.nav .menu .menu_inn .item .link {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
}

.nav .menu .menu_inn .item .link .tx {
  display: block;
  position: relative;
  padding-left: 26px;
}

.nav .menu .menu_inn .item .link .tx:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -9px;
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-size: 18px 18px;
}

.nav .menu .menu_inn .item.home .link .tx:before {
  background-image: url("../img/ic/ic_nav_home.png");
}

.nav .menu .menu_inn .item.calendar .link .tx:before {
  background-image: url("../img/ic/ic_nav_calendar.png");
}

.nav .menu .menu_inn .item.setup .link .tx:before {
  background-image: url("../img/ic/ic_nav_my.png");
}

.nav .menu .menu_inn .item .link .tx {
  font-size: 9px;
}

.nav .menu .menu_inn .item.on:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 2px;
  background-color: #666;
}

.nav .menu .menu_inn .item.on .tx:after {
  content: "선택됨";
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px 0 0 -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

/* header */
.header {
  position: relative;
}

.header .header_inn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 55px;
}

.header.main .header_inn {
  background-color: #BB2649;
}

.header .logo h1 {
  padding: 0;
  font-size: 19px;
  color: #000;
  line-height: 30px;
}

.header .logo h1 > strong {
  padding: 0;
  font-size: 20px;
  color: #000;
  line-height: 30px;
}

.header .left {
  position: absolute;
  top: 0px;
  left: 0;
  height: 55px;
  padding: 0 0 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.header .right {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 55px;
  padding: 0 20px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  z-index: 2;
}

.header .center {
  position: relative;
  width: calc(100% - 106px);
  z-index: 1;
}

.header .center .tit_h2 {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 17px;
  color: #000;
  text-align: center;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.header .btnClose {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 55px;
}

.header .btnClose:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 17px;
  height: 17px;
  margin: -8px 0 0 -8px;
  background: url("../img/bt/bt_close_lg.png") 0 0 no-repeat;
  background-size: 17px auto;
}

.header ~ .container {
  padding-top: 0;
  height: 100vh;
}

/* container */
/* .container{z-index:1000;background-color:#fff;} */
.content {
  padding: 0px 20px 0;
}

.fix_bottom {
  position: fixed;
  right: 0;
  bottom: -100%;
  left: 0;
  padding: 0;
  background-color: #fff;
  z-index: 1100;
  transition: bottom 0.2s;
}

.fix_bottom.show {
