.footer {
  background: transparent;
  bottom: 0;
  color: var(--text-color);
  height: 72px;
  left: 0;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 99;
}

.footer:before {
  content: "";
  background: #fff;
  border: 1px solid #eee;
  border-bottom: none;
  border-top-right-radius: 16px;
  display: block;
  height: 72px;
  width: 50%;
  position: absolute;
  left: -20px;
  top: 0;
}

.footer:after {
  content: "";
  background: #fff;
  border: 1px solid #eee;
  border-bottom: none;
  border-top-left-radius: 16px;
  display: block;
  height: 72px;
  width: 50%;
  position: absolute;
  right: -20px;
  top: 0;
  z-index: -1;
}

.footer > button {
  font-size: 0.75rem;
  border: none;
  background: none;
  cursor: pointer;
  padding-top: 26px;
  position: relative;
  min-width: 24px;
}

.footer > button:before {
  content: "";
  background-size: 100%;
  display: block;
  height: 24px;
  width: 24px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.f-1:before {
  background: url(../img/theme/apthefin/icon_apt.png) 0 0;
}

.f-2:before {
  background: url(../img/theme/apthefin/icon_house.png) 0 0;
}

.f-3:before {
  background: url(../img/theme/apthefin/icon_feed.png) no-repeat 2px 0;
  height: 68px !important;
  width: 70px !important;
  top: -25px !important;
  z-index: 999;
}

.f-4:before {
  background: url(../img/theme/apthefin/icon_profile.png) 0 0;
}

.f-5:before {
  background: url(../img/theme/apthefin/icon_network.png) 0 0;
}

.footer-back {
  background: #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  height: 45px;
  width: 50px;
}

