/* button */
.btnGroup {
  margin: 0 -5px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.btnGroup > p, .btnGroup > div {
  position: relative;
  padding: 0 5px 0px;
