/* button switch */
.btn_switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
}

.btn_switch input {
  position: absolute;
  width: 50px;
  height: 25px;
  margin: 0px;
  padding: 0;
  border: 0;
  -webkit-appearance: none;
  cursor: pointer;
}

.btn_switch .round {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #808080;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 12px;
}

.btn_switch .round:before {
  position: absolute;
  content: "";
  width: 21px;
  height: 21px;
  left: 3px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.btn_switch input:checked + .round:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  transform: translateX(24px);
}

.btn_switch .round:before {
  border-radius: 50%;
}

