.ico_sel_down {
  overflow: hidden;
  display: inline-block;
  position: relative;
  width: 11px;
  height: 12px;
}

.ico_sel_down:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 11px;
  height: 12px;
  margin-top: 0px;
  background: url("../img/ic/ic_sel_down.png") center center no-repeat;
  background-size: 11px 6px;
  text-indent: -500px;
  transition: all 0.5s ease;
}

.ico_sel_down:after {
  content: "펼침";
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

.open .ico_sel_down:before {
  content: "";
  transform: rotate(180deg);
}

.open .ico_sel_down:after {
  content: "접음";
}

