/* 아코디언 */
.accBox {
  border: 1px solid #E5E5E5;
  border-radius: 10px;
}

.accBox-tit {
  padding: 20px;
  display: block;
  font-size: 15px;
  font-weight: 500;
  line-height: 22px;
  color: #333;
  position: relative;
}

.accBox-tit:after {
  content: "";
  display: block;
  width: 11px;
  height: 6px;
  background: url("../img/ic/ic_arw_folding.png") no-repeat 50% 100%;
  background-size: 11px auto;
  position: absolute;
  right: 20px;
  top: 50%;
  margin-top: -3px;
}

.accBox-content {
  overflow: hidden;
  transition: all 0.3s;
  max-height: 0;
  border-top: 1px solid #e5e5e5;
  visibility: hidden;
}

.accBox-item:not(:first-of-type) {
  border-top: 1px solid #e5e5e5;
}

.accBox-item.-active .accBox-tit:after {
  transform: rotate(180deg);
}

.accBox-item.-active .accBox-tit ~ .accBox-content {
  max-height: 1000px;
  visibility: visible;
}

.accBox-inner {
  padding: 20px;
}

.accBox._gnb {
  border: none;
  border-radius: 0;
}

.accBox._gnb .accBox-content {
  background: #F1F2F5;
  padding-left: 20px;
  padding-right: 20px;
}

.accBox._gnb .accBox-inner {
  padding-left: 0;
  padding-right: 0;
}

.accBox._gnb .accBox-content a {
  font-size: 16px;
  display: block;
  width: 100%;
  position: relative;
}

.accBox._gnb .accBox-content a:after {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background: url("../img/theme/apthefin/icon/ic_menu_arrow.png") no-repeat 0 0;
  background-size: cover;
  right: 0;
  top: 50%;
  margin-top: -12px;
  position: absolute;
}

.accBox._gnb .accBox-inner:not(:first-of-type) {
  border-top: 1px solid #e5e5e5;
}

